'use client';
import { useState, useEffect } from 'react';
import { tmdbApi } from '@/lib/api/tmdb';
import MediaGrid from '@/components/media/MediaGrid';

export default function MoviesPage() {
  const [genres, setGenres] = useState([]);
  const [genreId, setGenreId] = useState<number | ''>('');
  const [order, setOrder] = useState<'popular' | 'latest'>('popular');
  const [movies, setMovies] = useState([]);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    (async () => {
      setLoading(true);
      setGenres(await tmdbApi.getMovieGenres());

      const data =
        genreId !== ''
          ? await tmdbApi.getMoviesByGenre(genreId, 1)
          : order === 'popular'
          ? await tmdbApi.getPopularMovies(1)
          : await tmdbApi.getUpcomingMovies(1);

      setMovies(data.results);
      setLoading(false);
    })();
  }, [genreId, order]);

  return (
    <main className="max-w-7xl mx-auto px-4 pt-10">
      <h1 className="text-3xl font-bold mb-8">Movies</h1>

      <div className="flex gap-4 mb-6">
        <select
          className="px-3 py-2 bg-gray-800 text-white rounded"
          value={genreId}
          onChange={e => setGenreId(e.target.value ? Number(e.target.value) : '')}
        >
          <option value="">All genres</option>
          {genres.map(g => (
            <option key={g.id} value={g.id}>
              {g.name}
            </option>
          ))}
        </select>

        <select
          className="px-3 py-2 bg-gray-800 text-white rounded"
          value={order}
          onChange={e => setOrder(e.target.value as any)}
        >
          <option value="popular">Most popular</option>
          <option value="latest">Latest releases</option>
        </select>
      </div>

      <MediaGrid items={movies} mediaType="movie" loading={loading} />
    </main>
  );
}
